<template>
	<view class="body">
		<web-view class="link" :src="data.url"></web-view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import {
		see_data, 
	} from '@/common/http.api.js';
	export default{
		data() {
			return {
				token:'',
				data:{}
			}
		},
		onLoad(option) {
			this.token = store.state.vuex_token
			see_data({token:this.token,id:option.id}).then(res=>{
				console.log(res)
				if(res.data.code==-1){
					this.$u.toast(res.data.data)
				}else{
					this.data = res.data.data
					if(this.data.url==''){
						this.$u.toast('无详情信息')
					}
				}
			}).catch(err=>{
				console.log(err)
			})
		},
	}
</script>

